import React from 'react';
import {TabPanel} from '@here/hds-tabs';

// eslint-disable-next-line no-new
new TabPanel();

interface HDSTabPanelProps extends React.HTMLAttributes<HTMLElement> {
  children: React.ReactNode;
  show?: boolean;
  ariaLabelledby?: string;
}
const HDSTabPanel = React.forwardRef(
  ({show, ariaLabelledby, children, className, ...rest}: HDSTabPanelProps, ref) => (
    <hds-tab-panel
      {...(className && {class: className})}
      {...(ariaLabelledby && {'aria-labelledby': ariaLabelledby})}
      icon={show}
      ref={ref}
      {...rest}
    >
      {children}
    </hds-tab-panel>
  )
);

HDSTabPanel.defaultProps = {
  show: false,
  ariaLabelledby: '',
};

export default HDSTabPanel;
